.about {
  margin-bottom: 110px;
}

.about h2 {
  margin: 0 0 22px;
  font-family: $roboto;
  font-weight: normal;
  font-size: 2.25rem;
  line-height: 3.19rem;
}

.about__wrapper {
  display: flex;
}

.about p {
  margin: 0 0 22px;
  line-height: 1.37rem;
}

.about__link {
  margin-top: 19px;
  padding-right: 49px;
  padding-left: 49px;
  color: $blue;
  border-color: $blue;
  transition: background-color $easy;

  &:hover {
    color: $white;
    background-color: $blue;
  }
}

.about__txt-wrapper {
  position: relative;
  padding-left: 180px;
  padding-right: 5px;

  // &::after {
  //   content: "2005";
  //   position: absolute;
  //   top: 50%;
  //   left: 0;
  //   font-family: $roboto;
  //   font-weight: 700;
  //   font-size: 10rem;
  //   line-height: 0;
  //   transform: rotate(-90deg);
  // }

  @media (max-width: $mobile) {
    padding: 0;
  }
}

.about__year {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  font-family: $roboto;
  font-weight: 700;
  font-size: 160px;
  line-height: 119px;
  letter-spacing: -2px;
  background-repeat: no-repeat;
  background-size: cover;

  background-image: url("../img/about-bg@1x.png");
  background-clip: text;
  color: transparent;

  transform-origin: 9px 7px;
  transform: rotate(270deg) translateX(-50%);

  @media (max-width: $mobile) {
    display: none;
  }
}
